import { getData, filterSize, toSort } from "../utils/products";

const products = {
  namespace: 'products',
  state: {
    origin: [],
    data: [],
    loading: true
  },
  reducers: {
    'save'(state, { payload }) {
      let obj = {
        origin: [...payload.data],
        data: [...payload.data],
        loading: payload.loading
      }
      state = { ...obj };
      return state;
    },
    'update'(state, { payload }) {
      state.data = payload.data;
      state.loading = payload.loading;
      state = { ...state };
      return state;
    }
  },
  effects: {
    *getProducts(action, { call, put }) {
      const data = yield call(getData);
      yield put({ type: 'save', payload: { data, loading: false } });
    },
    *filterSizes({ payload }, { call, put }) {
      const data = yield call(filterSize, payload);
      yield put({ type: 'update', payload: {data, loading: false} });
    },
    *sortProducts({ payload }, { call, put }) {
      const data = yield call(toSort, payload);
      yield put({ type: 'update', payload: { data, loading: false } });
    }
  }
}

export default products;