import axios from 'axios';

// 初始化获取数据
export const getData = () => {
  let data;
  return (axios.get('products.json')
    .then(res => {
      data = res.data.products;
      return data;
    }));
}

// 尺寸过滤(同时保留当前排序类型)
let sizeArr = [];
let method;
export const filterSize = ({ sizes, origin }) => {
  let newData = [...origin];
  switch (method) {
    case '+price':
      newData = newData.sort((a,b)=>a.price-b.price);
      break;
    case '-price':
      newData = newData.sort((a,b)=>b.price-a.price);
      break;
    default:
      break;
  }
  let index = sizeArr.indexOf(sizes);
  if (index === -1) {
    sizeArr.push(sizes);
  } else {
    sizeArr.splice(index, 1);
  }
  sizeArr.forEach(size => {
    newData = newData.filter(item => item.availableSizes.indexOf(size) !== -1);
  });
  return newData;
}

// 排序(同时保留当前尺寸过滤)
export const toSort = ({ data, way, origin }) => {
  method = way;
  switch (way) {
    case 'default':
      sizeArr.forEach(size=>{
        origin = origin.filter(item => item.availableSizes.indexOf(size) !== -1);
      });
      return origin;
    case '+price':
      return data.sort((a,b)=>a.price-b.price);
    case '-price':
      return data.sort((a,b)=>b.price-a.price);
    default:
      return data;
  }
}